/*
 *  Contact
 *    Styles for the contact section
 *
 *  Version:
 *    1.0.0 - 2021/09/23
 */

.c-contact {
  background-color: $s-color--background-subdued;
  text-align: center;
  padding: 3.6875rem 1.1875rem 7.5rem 1.25rem;

  &__heading {
    color: $s-color--font;
  }

  &__subheading {
    color: $s-color--font;
    margin-top: 1rem;
    margin-bottom: 0;
  }

  &__faq {
    margin-top: 1.5rem;

    &-link {
      color: $s-color--font-link;
      border-bottom: 0.0625rem solid $s-color--font-link;
    }
  }

  &__text-blocks {
    margin: 0 auto;
    text-align: left;
    max-width: 43.75rem;

    &-wrapper {
      color: $s-color--font;

      &:not(:first-child) {
        margin-top: 4.9375rem;
      }
    }

    &-heading {
      margin-bottom: 1.1875rem;
    }
  }

  &__select {
    background-color: $s-color--background;
    padding: 1rem 2.5rem 1rem 1.25rem;
    border: 0.0625rem solid $s-color--border-focus;
    border-radius: 0.25rem;
    outline: none;
    width: 100%;
    color: $s-color--font;
    opacity: 0.9;

    &:focus {
      border: 0.0625rem solid $s-color--border-focus;
      border-radius: 0.25rem;
    }

    &-wrapper {
      margin: 2.5rem auto 0;
      position: relative;
      max-width: 18.5rem;
      width: 100%;

      &::after {
        content: '';
        display: block;
        position: absolute;
        height: 0.875rem;
        width: 0.875rem;
        top: 50%;
        right: 1.25rem;
        background-image: url("./../../assets/icon-arrow-down.svg");
        background-repeat: no-repeat;
        background-position: center;
        transform: translateY(-50%);
        pointer-events: none;
        z-index: 5;
      }
    }
  }

  &__form {
    &-input-wrapper {
      padding: 2rem 0 7.5625rem;
      text-align: center;
    }
  }

  &__input {
    position: relative;
    padding-right: 1.875rem;
    display: block;
    margin-bottom: 0.875rem;

    &.has-error {
      border: 0.0625rem solid $s-color--border-error;
    }

    &--textarea {
      min-height: 12.5rem;
    }
  }

  &__optional {
    &-wrapper {
      position: relative;
    }

    &-tag {
      @include t-font--paragraph-2;
      display: none;
      position: absolute;
      opacity: 0.4;
      top: 1rem;
      right: 1.5rem;
    }
  }

  &__button {
    min-width: 10.0625rem;
    margin-top: 0.4375rem;
    border-radius: 6.25rem;
  }

  &__success {
    margin-top: 4.375rem;
  }

  &__errors {
    margin-top: 4.375rem;
    color: $s-color--error;
  }

  &__button-top {
    margin-top: 4.75rem;
    border-radius: 6.25rem;
    margin-bottom: 8.5rem;
  }

  /*** Media Queries ***/

  @include t-media-query ($s-small-up) {
    &__select {
      padding: 1rem 12rem 1rem 1.25rem;

      &-wrapper {
        margin-top: 3.875rem;
        max-width: 25rem;
      }
    }

    &__form-input-wrapper {
      max-width: 53.75rem;
      margin: 0 auto;
      padding-top: 2.875rem;
      padding-bottom: 8.375rem;
    }

    &__input {
      &:nth-child(2) {
        margin-left: 1rem;
      }

      &-group {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
    }

    &__optional-tag.is-visible {
      display: block;
    }

    &__input--textarea {
      min-height: 10rem;
    }

    &__button {
      @include t-font--paragraph-1-desktop;
      width: 16.0625rem;
      margin-top: 2rem;
    }
  }

  @include t-media-query($s-medium-up) {
    padding-top: 6.25rem;
    padding-bottom: 12.8125rem;

    &__heading {
      margin-bottom: 0;
      @include t-font--heading-5-laptop;
    }

    &__subheading {
      margin-top: 0.75rem;
      @include t-font--heading-1-laptop;
    }

    &__faq {
      @include t-font--paragraph-1-desktop;
      margin-top: 2.5625rem;

      &-link {
        padding-bottom: 0.375rem;
      }
    }

    &__text-blocks {
      text-align: center;

      &-wrapper {
        &:not(:first-child) {
          margin-top: 4.9375rem;
        }
      }

      &-heading {
        margin-bottom: 0.75rem;
        font-size: 1.875rem;
      }
    }
  }
}
